import React, { useState, useEffect } from 'react';
import "./AuthForm.css"
interface IUserData {  
    username?: string;  
    password?: string;
    first_name?: string;
    last_name?: string;
}  

interface ISignup {  
    handle_signup  : (userData: IUserData) => void;
    display_form : (formType:string) => void;
    clearError : () => void;
} 

const SignupForm = (props: ISignup) => {

    const [username,setUsername] = useState("")
    const [password, setPassword] = useState("")
    const [first_name, setFirstName] = useState("")
    const [last_name, setLastName] = useState("")

    const submitForm = (e: React.SyntheticEvent<EventTarget>) => {
        e.preventDefault()
        const target = e.target as typeof e.target & {
            username: { value: string };
            password: { value: string };
            first_name : { value: string };
            last_name : { value: string };
        };
        const username = target.username.value; // typechecks!
        const password = target.password.value; // typechecks!
        const first_name = target.first_name.value; // typechecks!
        const last_name = target.last_name.value; // typechecks!
        const user = {
            username : username,
            password : password,
            first_name : first_name,
            last_name : last_name
        }
        props.display_form("")
        props.handle_signup(user)
    }
    const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const newValue = e.target.value;
        const name = e.target.name;
        switch(name) {
            case "username":
                return setUsername(newValue)
            case "password":
                return setPassword(newValue)
            case "first_name":
                return setFirstName(newValue)
            case "last_name":
                return setLastName(newValue)
            default:
                return
        }
    }

    const clearForm = () => {
        setUsername("")
        setPassword("")
        setFirstName("")
        setLastName("")
        props.clearError()
    }
    return (
        <form onSubmit={submitForm}>
            <div className="auth-input-field">
                <div><label>Username</label></div>
                <input type="text" name="username"/>
            </div>
            <div className="auth-input-field">
                <div><label>Password</label></div>
                <input type="password" name="password"/>
            </div>
            <div className="auth-input-field">
                <div><label>First Name</label></div>
                <input type="text" name="first_name"/>
            </div>
            <div className="auth-input-field">
                <div><label>Last Name</label></div>
                <input type="text" name="last_name"/>
            </div>
            <div className="auth-buttons">
                <input className="auth-button no-auth-button" type="button" value="BACK" onClick={()=>props.display_form("login")}/>
                <input className="auth-button no-auth-button" type="button" value="CLEAR" onClick={clearForm}/>
                <input className="auth-button" type="submit" value="SUBMIT"/>
            </div>
        </form>
    )
}
export default SignupForm