import React, { useState, useEffect } from 'react';
import './AuthForm.css';
import SignupForm from '../form/SignupForm'
interface IUserData {  
    username?: string;  
    password?: string;
    first_name?: string;
    last_name?: string;
}  

interface IloginIn {  
    handle_login  : (userData: IUserData) => void;
    handle_signup : (userData: IUserData) => void;
    username : string;
    showLogInForm : boolean;
    showSignUpForm : boolean;
    display_form : (formType: string) => void;
    clearError : () => void;
} 

const LoginForm = (props:IloginIn) => {

    const [username,setUsername] = useState("")
    const [password, setPassword] = useState("")

    const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const newValue = e.target.value;
        const name = e.target.name;
        name === "username" ? setUsername(newValue) : setPassword(newValue)
    }
    const clearForm = () => {
        setUsername("")
        setPassword("")
        props.clearError()
    }
    const submitForm = (e: React.SyntheticEvent<EventTarget>) => {
        e.preventDefault()
        const target = e.target as typeof e.target & {
            username: { value: string };
            password: { value: string };
        };
        const username = target.username.value; // typechecks!
        const password = target.password.value; // typechecks!

        const user = {
            username : username,
            password : password,
        }
        props.handle_login(user)
    }
    return (
        <div>
            { props.showLogInForm && !props.showSignUpForm && 
                <div className='auth-form'>
                    <h1>LOGIN</h1>
                    <form onSubmit={submitForm}>
                        <div className="auth-input-field">
                            <label>Username</label>
                            <input type="text" name="username" value={username} onChange={handleChange}/>
                        </div>
                        <div className="auth-input-field">
                            <label>Password</label>
                            <input type="password" name="password" value={password} onChange={handleChange}/>
                        </div>
                        <div className="auth-buttons">
                            <input className="auth-button no-auth-button" type="button" value="CLEAR" onClick={clearForm}/>
                            <input className="auth-button" type="submit" value="SUBMIT"/>
                        </div>
                        <div className="auth-action-link">
                            <div onClick={()=>props.display_form("signup")}>Register Now</div>
                            <div>Forgot Password</div>
                        </div>
                    </form>
                </div>
                }
                { props.showSignUpForm && !props.showLogInForm && 
                <div className='auth-form auth-signup-form'>
                    <h1>REGISTRATION</h1>
                    <SignupForm handle_signup={props.handle_signup} display_form={props.display_form} clearError={props.clearError}></SignupForm>
                </div>
            }
        </div>
    )
}
export default LoginForm